import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import "./App.css";
import Header from "./componentes/Header/Header";
import Home from "./componentes/Home/Home";
import Botones from "./componentes/Botones/Botones";
import { ListadoChats } from "./componentes/Listado chats/ListadoChats";
import ChatPersona from "./componentes/Chat Persona/ChatPersona";
import personasChat from "./personasChat";
import Error from "./componentes/Error/Error";

function App() {
  return (
    <div className="App">
      <Router basename={"/"} >
        <Routes>
          {personasChat.map((persona) => (
            <Route
              path={`/chats/${persona.nombre}`}
              element={[
                <Header botonRetroceder={"/chats"} />,
                <ChatPersona
                  nombre={persona.nombre}
                  imagen={persona.imagen}
                  mensaje={persona.mensaje}
                />,
              ]}
            />
          ))}

          <Route
            path="chats"
            element={[<Header botonRetroceder={"/"} />, <ListadoChats />]}
          />

          <Route path="/" element={[<Header />, <Home />, <Botones />]} />

          <Route path="*" element={<Error />} />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
