import { Avatar } from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";
import "./chats.css";

const Chats = ({ nombre, mensaje, time, imgPerfil }) => {
  return (
      <Link to={`${nombre}`}>
    <div className="chats">
      <Avatar className="chats__avatar" alt={nombre} src={imgPerfil} />
      <div className="chats__detalles">
          <h2>{nombre}</h2>
          <p>{mensaje}</p>
      </div>
      <p className="chats__time">
        {time}
      </p>
    </div>
    </Link>
  );
};

export default Chats;
