import React from "react";
import ForumIcon from "@mui/icons-material/Forum";
import PersonIcon from "@mui/icons-material/Person";
import { IconButton } from "@material-ui/core";
import "./header.css";
import { Link, useNavigate } from "react-router-dom";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";

const tinderLogo = "https://cdn.worldvectorlogo.com/logos/tinder-2.svg";

const Header = ({ botonRetroceder, opacity }) => {
  const navigate = useNavigate();
  return (
    <div className="header">
      {botonRetroceder ? (
        <IconButton onClick={() => navigate(botonRetroceder, {replace: true})}>
          <ArrowBackIosIcon fontSize="large" className="header__retroceder" />
        </IconButton>
      ) : (
        
        <IconButton>
          <PersonIcon fontSize="large" className="header__icon" />
        </IconButton>
        
      )}

      <Link to="/">
        <img src={tinderLogo} alt="tinder icon" className="header__logo" />
      </Link>

      <Link to="/chats" >
        <IconButton>
          <ForumIcon fontSize="large" className="header__icon" />
        </IconButton>
      </Link>
    </div>
  );
};

export default Header;
