import React from "react";
import "./home.css";
import { useState } from "react";
import TarjetaPersona from "react-tinder-card";
import fakePersonas from "../../fakeApi";



const Home = () => {
  const [personas, setPersonas] = useState(fakePersonas);


  return (
    <div className="home">
      <div className="tarjetas__contenedor">
        {personas.map((persona) => (
          <TarjetaPersona
            className="swipe"
            key={persona.id}
            preventSwipe={["up", "down"]}
          >
            <div
              className="tarjeta"
              style={{ backgroundImage: `url(${persona.url})` }}
            >
              <div className="persona__info">
                <h2>{persona.nombre} </h2>
                <p>{persona.edad} </p>
              </div>

              <p className="distancia">{persona.distancia}</p>
            </div>
          </TarjetaPersona>
        ))}
      </div>
    </div>
  );
};

export default Home;
