import React from "react";
import "./botones.css";
import ReplayIcon from "@mui/icons-material/Replay";
import CloseIcon from "@mui/icons-material/Close";
import StarIcon from "@mui/icons-material/Star";
import FavoriteIcon from "@mui/icons-material/Favorite";
import FlashOnIcon from "@mui/icons-material/FlashOn";
import { IconButton } from "@material-ui/core";

const Botones = () => {
  return (
    <div className="botones-swipe">
      <IconButton className="botonesSwipe__replay" id="btn-replay">
        <ReplayIcon fontSize="large"  />
      </IconButton>

      <IconButton>
        <CloseIcon fontSize="large" className="botonesSwipe__close" />
      </IconButton>

      <IconButton>
        <StarIcon fontSize="large" className="botonesSwipe__star" />
      </IconButton>

      <IconButton>
        <FavoriteIcon fontSize="large" className="botonesSwipe__fav" />
      </IconButton>

      <IconButton>
        <FlashOnIcon fontSize="large" className="botonesSwipe__flash" />
      </IconButton>
    </div>
  );
};

export default Botones;
